











































import { Component, Vue } from "vue-property-decorator"
import ItemData from '../model/ItemData'


// 必须使用 @Component 才能访问实例
@Component({})
export default class MemoEditor extends Vue {
    // 保存编辑框数据
    memo: ItemData = new ItemData( -1, 0 )
    // 保存新文章
    saveNew(){
        // 1. 验证 memo 数据是否合格
        if( this.memo && this.memo.categoryId > -1 && this.memo.title.trim() !== "" && this.memo.content.trim() !== "" ){
            // 判定是新增 还是 修改 
            if( this.memo.id === -1 ){
              // 2. 将验证合格的 neno 对象添加在 memoList 数组，保存到 localStorage 中
              this.$store.state.aHelper.add( this.memo )
            } else {
              this.$store.state.aHelper.edit( this.memo )
            }
            this.$store.state.showEditor = false
        } else {
            alert( "对不起，请输入完整的信息" )
        }
    }
    closeWin(): void {
        this.$store.state.showEditor = false
    }
    // 当组件每次创建显示时
    created(): void{
        // 接收 store 中的 memo
        this.memo = this.$store.state.transMemo
    }
}

